<!-- template for the modal component -->
<template>
    <transition name="modal">
        <div class="modal-mask confirm_modal">
            <div class="modal-wrapper">
                <div class="modal-container">
                    <div class="head-content">
                        <div class="right-content">
                            <button type="button" class="btn btn-modal close" aria-label="Close" @click="$emit('close')">
                                <span aria-hidden="true" style="font-size: 45px;">&times;</span>
                            </button>
                        </div>
                        
                    </div>
                    <div class="result" v-if="showResult">
                        <div class="title-confirm">
                            <svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" fill="currentColor" class="bi bi-check-circle" viewBox="0 0 40 40">
                                <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z"/>
                                <path d="M10.97 4.97a.235.235 0 0 0-.02.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-1.071-1.05z"/>
                            </svg>
                            <h3>Deleted Successfully!</h3>
                        </div>
                    </div>
                    <div class="action" v-if="!showResult">
                        <h3>Do you want to remove this order from the plotter?</h3>
                        <button class="btn btn-primary" @click="confirmYes">Yes</button>
                        <button class="btn btn-primary" @click="confirmNo">No</button>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>


<script >
    export default {
        name: 'Confirm',
        props: {
            
        },
        data: function() {
            return {
                showResult: false
            }
        },
        mounted() {
            var self = this;
            this.$root.$on('confirmCompleted', function(action) {
                self.showResult = action;
            });
        },
        methods: {
            confirmYes() {
                this.$root.$emit('confirmAction', true);
            },
            confirmNo() {
                this.$root.$emit('confirmAction', false);
            }
        },
        components: {
            
        }
    } 
</script>
<style scoped>
.title-confirm svg{
    fill: #00af5e;
}
.modal-mask {
    position: fixed;
    z-index: 9998;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    display: table;
    transition: opacity .3s ease;
}

.modal-wrapper {
    display: table-cell;
    vertical-align: middle;
}

.modal-container {
    width: 500px;
    padding: 20px 50px;
    margin: 0px auto;
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
    transition: all .3s ease;
    font-family: Helvetica, Arial, sans-serif;
}

.modal-header h3 {
    margin-top: 0;
    color: #42b983;
}

.modal-body {
    margin: 20px 0;
}

.modal-default-button {
    float: right;
}

/*
   * The following styles are auto-applied to elements with
   * transition="modal" when their visibility is toggled
   * by Vue.js.
   *
   * You can easily play with the modal transition by editing
   * these styles.
   */

.modal-enter {
    opacity: 0;
}

.modal-leave-active {
    opacity: 0;
}

.title-modal {
    text-align: left;
    float: left;
}

.right-content {
    text-align: right;
    padding-right: 5px;
    cursor: pointer;
}
.right-content .order-id {
    margin-right: 20px;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
}

.modal-footer {
    clear: left;
    width: 100%;
}

</style>


