<template>
    <div class="group-action">
        <table class="action-table">
            <tr>
                <td>
                    <svg width="1em" height="1em" viewBox="0 0 16 16" class="bi bi-calendar3" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                        <path fill-rule="evenodd" d="M14 0H2a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2zM1 3.857C1 3.384 1.448 3 2 3h12c.552 0 1 .384 1 .857v10.286c0 .473-.448.857-1 .857H2c-.552 0-1-.384-1-.857V3.857z" />
                        <path fill-rule="evenodd" d="M6.5 7a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm3 0a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm3 0a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm-9 3a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm3 0a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm3 0a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm3 0a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm-9 3a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm3 0a1 1 0 1 0 0-2 1 1 0 0 0 0 2zm3 0a1 1 0 1 0 0-2 1 1 0 0 0 0 2z" />
                    </svg>
                </td>
                <td><a v-bind:href="linkOrderDetail" target ="_blank">Order Details</a></td>
            </tr>
            <tr>
                <td>
                    <svg width="1em" height="1em" viewBox="0 0 16 16" class="bi bi-calendar2-week" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                        <path fill-rule="evenodd" d="M3.5 0a.5.5 0 0 1 .5.5V1h8V.5a.5.5 0 0 1 1 0V1h1a2 2 0 0 1 2 2v11a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2V3a2 2 0 0 1 2-2h1V.5a.5.5 0 0 1 .5-.5zM2 2a1 1 0 0 0-1 1v11a1 1 0 0 0 1 1h12a1 1 0 0 0 1-1V3a1 1 0 0 0-1-1H2z" />
                        <path d="M2.5 4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5H3a.5.5 0 0 1-.5-.5V4zM11 7.5a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-1zm-3 0a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-1zm-5 3a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-1zm3 0a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5h-1a.5.5 0 0 1-.5-.5v-1z" />
                    </svg>
                </td>
                <td><a href="#" @click="changeDateOrderPlotter">Change Date</a></td>
            </tr>
            <tr>
                <td>
                    <svg color="red" width="1em" height="1em" viewBox="0 0 16 16" class="bi bi-x" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                        <path fill-rule="evenodd" d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z" />
                    </svg>
                </td>
                <td><a href="#" @click="removeOrderPlotter">Remove</a></td>
            </tr>
        </table>
        <ChangeDate :OrderId="OrderId" :TimeId="TimeId" :index="index" v-if="showChangeDate" @close="closePopup()"></ChangeDate>
        <Confirm :OrderId="OrderId" v-if="showConfirm" @close="closePopup()"></Confirm>
    </div>
</template>

<script>
import WooCommerceRestApi from "@woocommerce/woocommerce-rest-api"
import Confirm from "../popup/Confirm"
import ChangeDate from "../popup/ChangeDate"
export default {
    name: 'GroupAction',
    data() {
        return {
            showChangeDate: false,
            showConfirm: false,
        }
    },
    props: {
        OrderId: String,
        linkOrderDetail: String,
        TimeId: Number,
        index: Number,
    },
    mounted() {
        self = this;
        this.$root.$on('cancelChangeDate', function(action) {
            self.showChangeDate = action;
        })
    },
    methods: {
        check: function() {
            alert(this.OrderId);
        },
        removeOrderPlotter: function() {
            var self = this;
            this.showConfirm = true;
            var orderId = this.OrderId;
            var action = 'delete';
            this.$root.$on('confirmAction', function(actionCf) {

                if(actionCf) {
                    var WooCommerce = new WooCommerceRestApi({
                        url: self.$parent.$parent.$parent.config.url,
                        consumerKey: self.$parent.$parent.$parent.config.consumerKey,
                        consumerSecret: self.$parent.$parent.$parent.config.consumerSecret,
                        wpAPI: true,
                        version: 'wc/v3',
                    });
                    WooCommerce.put('custom/plotter/'+orderId, { action : action}).then(function(result) {
                        if(result.data) {
                            self.$root.$emit('confirmCompleted', true);
                            self.$root.$emit('removeOrderPlotter', result.data, self.TimeId, self.index );
                        }
                    })
                } else {
                    self.showConfirm = false;
                }
            });
           
        },
        changeDateOrderPlotter: function() {
            this.showChangeDate = true;
        },
        closePopup: function() {
            this.showChangeDate = false;
            this.showConfirm = false;
        }
    },
    components: {
        WooCommerceRestApi,
        ChangeDate,
        Confirm
    }
}
</script>
<style scoped>
.group-action {
    /*height: 100px;*/
    /*width: 100px;*/
    background-color: white;
    z-index: 100;
    position: absolute;
    top: 25 px;
    right: 0;
    border: 1px solid silver;
}

.action-table {
    width: 150 px;
}

.action-table a {
    text-decoration: none;
}
</style>



