<!-- template for the modal component -->
<template>
    <transition name="modal">
        <div class="modal-mask">
            <div class="modal-wrapper">
                <div class="modal-container">
                    <div class="head-content">
                        <h3 class="title-modal">Change Date</h3>
                        <div class="right-content">
                            <span class="order-id"><h2 style="display: inline-block;">{{OrderId}}</h2></span>
                            <button type="button" class="btn btn-modal close" aria-label="Close" @click="$emit('close')">
                                <span aria-hidden="true" style="font-size: 45px;">&times;</span>
                            </button>
                        </div>
                        
                    </div>
                    <table class="table table-striped">
                          <thead>
                              <tr>
                                  <td>New Date</td>
                                  <td>New Period</td>
                              </tr>
                          </thead> 
                          <tbody>
                              <tr>
                                <td><input type="date" class="form-control" v-model="dateSelected"></td>
                                <td>
                                    <select name="period" class="form-control" v-model="period">
                                        <option v-bind:value="period.value" v-for="period in periods">{{period.label}}</option>
                                    </select>
                                </td>
                              </tr>
                          </tbody>   
                    </table>
                    <button @click="changeDate" class="btn btn-primary" >Save</button>
                    <button @click="cancelChangeDate" class="btn">Cancel</button>
                </div>
            </div>
            <SaveOrder v-if="showSaveOrder" @close="closePopup()"></SaveOrder>
        </div>
    </transition>
</template>


<script >
    import WooCommerceRestApi from "@woocommerce/woocommerce-rest-api";
    import SaveOrder from "../popup/SaveOrder"
    export default {
        name: 'ChangeDate',
        props: {
            OrderId: '',
            TimeId: Number,
            index: Number,
        },
        data: function() {
            return {
                periods: [],
                period: String, 
                dateSelected: String,
                showSaveOrder: false 
            }
        },
        mounted() {
            this.periods = this.$parent.$parent.$parent.$parent.periods
        },
        methods: {
            changeDate() {
                var period = this.period;
                var date = this.dateSelected;
                var order_id = this.OrderId;
                var self = this;
                var WooCommerce = new WooCommerceRestApi({
                    url: self.$parent.$parent.$parent.$parent.config.url,
                    consumerKey: self.$parent.$parent.$parent.$parent.config.consumerKey,
                    consumerSecret: self.$parent.$parent.$parent.$parent.config.consumerSecret,
                    wpAPI: true,
                    version: 'wc/v3',
                });
                if(order_id && period && date) {
                    self.showSaveOrder = true;
                    WooCommerce.put('custom/plotter-search', {day : date, period : period, order_id : order_id }).then(function(result) {
                        if(result.data) {
                            self.$root.$emit('saveOrderPlotter', true);
                            self.$root.$emit('removeOrderPlotter', result.data, self.TimeId, self.index );
                        }
                    });
                } 
            },
            cancelChangeDate() {
                this.$root.$emit('cancelChangeDate', false);
            },
            closePopup: function() {
                this.showSaveOrder = false;
            }
        },
        components: {
            WooCommerceRestApi,
            SaveOrder
        }
    } 
</script>
<style scoped>
.modal-mask {
    position: fixed;
    z-index: 9998;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    display: table;
    transition: opacity .3s ease;
}

.modal-wrapper {
    display: table-cell;
    vertical-align: middle;
}

.modal-container {
    width: 45%;
    margin: 0px auto;
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
    transition: all .3s ease;
    font-family: Helvetica, Arial, sans-serif;
}

.modal-header h3 {
    margin-top: 0;
    color: #42b983;
}

.modal-body {
    margin: 20px 0;
}

.modal-default-button {
    float: right;
}

/*
   * The following styles are auto-applied to elements with
   * transition="modal" when their visibility is toggled
   * by Vue.js.
   *
   * You can easily play with the modal transition by editing
   * these styles.
   */

.modal-enter {
    opacity: 0;
}

.modal-leave-active {
    opacity: 0;
}

.title-modal {
    text-align: left;
    float: left;
}

.right-content {
    text-align: right;
    padding-right: 5px;
    cursor: pointer;
}
.right-content .order-id {
    margin-right: 20px;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
}

.modal-footer {
    clear: left;
    width: 100%;
}

</style>


