                        </svg>

                        <!--         <div class="group-action"></div>-->
                        <GroupAction v-show="order.showGroupAction" :TimeId="TimeId" :index="order.index" :OrderId="order.orderId" :linkOrderDetail="order.linkOrderDetail"></GroupAction>
                    </div>
                </h3>
            </div>

            <div v-show="order.showDetail" class="order-detail">
                <table class="order-table table">
                    <tr>
                        <td class="title">Delivery Address: </td>
                        <td class="content">
                            <span v-if="order.editAddress" v-html="order.orderDetail.address"></span>
                            <input v-else-if="!order.editAddress" class="order-detail form-control" :ref="'delivery'+order.orderId" v-bind:readonly="order.editAddress" type="text" v-model="order.orderDetail.address">
                        </td>
                        <td class="action">
                            <svg v-if="order.editAddress" v-on:click="editInfo('address',order, $event)" xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-pencil-square" viewBox="0 0 16 16">
                                <path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456l-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z"/>
                                <path fill-rule="evenodd" d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"/>
                            </svg>

                            <svg v-else-if="!order.editAddress" v-on:click="saveInfo('address',order, $event , order.orderDetail.address)" width="1em" height="1em" viewBox="0 0 16 16" class="bi bi-calendar2-check" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                <path fill-rule="evenodd" d="M3.5 0a.5.5 0 0 1 .5.5V1h8V.5a.5.5 0 0 1 1 0V1h1a2 2 0 0 1 2 2v11a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2V3a2 2 0 0 1 2-2h1V.5a.5.5 0 0 1 .5-.5zM2 2a1 1 0 0 0-1 1v11a1 1 0 0 0 1 1h12a1 1 0 0 0 1-1V3a1 1 0 0 0-1-1H2z" />
                                <path d="M2.5 4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5H3a.5.5 0 0 1-.5-.5V4z" />
                                <path fill-rule="evenodd" d="M10.854 8.146a.5.5 0 0 1 0 .708l-3 3a.5.5 0 0 1-.708 0l-1.5-1.5a.5.5 0 0 1 .708-.708L7.5 10.793l2.646-2.647a.5.5 0 0 1 .708 0z" />
                            </svg>
                        </td>
                    </tr>
                    <tr>
                        <td class="title">Name: </td>
                        <td class="content">
                            <span v-if="order.editName" v-html="order.orderDetail.name"></span>
                            <input v-else-if="!order.editName" class="order-detail form-control"  :ref="'name'+order.orderId" v-bind:readonly="order.editName" type="text" v-model="order.orderDetail.name">
                        </td>
                        <td class="action">
                            <svg v-if="order.editName" v-on:click="editInfo('name',order,!order.editName, $event)" xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-pencil-square" viewBox="0 0 16 16">
                                <path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456l-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z"/>
                                <path fill-rule="evenodd" d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"/>
                            </svg>

                            <svg v-else-if="!order.editName" v-on:click="saveInfo('name', order, $event , order.orderDetail.name)" width="1em" height="1em" viewBox="0 0 16 16" class="bi bi-calendar2-check" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                <path fill-rule="evenodd" d="M3.5 0a.5.5 0 0 1 .5.5V1h8V.5a.5.5 0 0 1 1 0V1h1a2 2 0 0 1 2 2v11a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2V3a2 2 0 0 1 2-2h1V.5a.5.5 0 0 1 .5-.5zM2 2a1 1 0 0 0-1 1v11a1 1 0 0 0 1 1h12a1 1 0 0 0 1-1V3a1 1 0 0 0-1-1H2z" />
                                <path d="M2.5 4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5H3a.5.5 0 0 1-.5-.5V4z" />
                                <path fill-rule="evenodd" d="M10.854 8.146a.5.5 0 0 1 0 .708l-3 3a.5.5 0 0 1-.708 0l-1.5-1.5a.5.5 0 0 1 .708-.708L7.5 10.793l2.646-2.647a.5.5 0 0 1 .708 0z" />
                            </svg>
                        </td>
                    </tr>
                    <tr>
                        <td class="title">Phone: </td>
                        <td class="content">
                            <span v-if="order.editPhone" v-html="order.orderDetail.phone"></span>
                            <input v-else-if="!order.editPhone" class="order-detail form-control" :ref="'phone'+order.orderId" v-bind:readonly="order.editPhone" type="text" v-model="order.orderDetail.phone">
                        </td>
                        <td class="action">
                            <svg v-if="order.editPhone" v-on:click="editInfo('phone',order,!order.editPhone, $event)" xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-pencil-square" viewBox="0 0 16 16">
                                <path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456l-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z"/>
                                <path fill-rule="evenodd" d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"/>
                            </svg>

                            <svg v-else-if="!order.editPhone" v-on:click="saveInfo('phone', order, $event , order.orderDetail.phone)" width="1em" height="1em" viewBox="0 0 16 16" class="bi bi-calendar2-check" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                <path fill-rule="evenodd" d="M3.5 0a.5.5 0 0 1 .5.5V1h8V.5a.5.5 0 0 1 1 0V1h1a2 2 0 0 1 2 2v11a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2V3a2 2 0 0 1 2-2h1V.5a.5.5 0 0 1 .5-.5zM2 2a1 1 0 0 0-1 1v11a1 1 0 0 0 1 1h12a1 1 0 0 0 1-1V3a1 1 0 0 0-1-1H2z" />
                                <path d="M2.5 4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5H3a.5.5 0 0 1-.5-.5V4z" />
                                <path fill-rule="evenodd" d="M10.854 8.146a.5.5 0 0 1 0 .708l-3 3a.5.5 0 0 1-.708 0l-1.5-1.5a.5.5 0 0 1 .708-.708L7.5 10.793l2.646-2.647a.5.5 0 0 1 .708 0z" />
                            </svg>
                        </td>
                    </tr>
                    <tr>
                        <td class="title">Remarks: </td>
                        <td class="content">
                            <span v-if="order.editRemarks" v-html="order.orderDetail.remarks"></span>
                            <input v-else-if="!order.editRemarks" class="order-detail form-control" :ref="'remarks'+order.orderId" v-bind:readonly="order.editRemarks" type="text" v-model="order.orderDetail.remarks">
                        </td>
                        <td class="action">
                            <svg v-if="order.editRemarks" v-on:click="editInfo('remarks',order,!order.editRemarks, $event)" xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-pencil-square" viewBox="0 0 16 16">
                                <path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456l-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z"/>
                                <path fill-rule="evenodd" d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"/>
                            </svg>

                            <svg v-else-if="!order.editRemarks" v-on:click="saveInfo('remarks', order, $event  , order.orderDetail.remarks)" width="1em" height="1em" viewBox="0 0 16 16" class="bi bi-calendar2-check" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                <path fill-rule="evenodd" d="M3.5 0a.5.5 0 0 1 .5.5V1h8V.5a.5.5 0 0 1 1 0V1h1a2 2 0 0 1 2 2v11a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2V3a2 2 0 0 1 2-2h1V.5a.5.5 0 0 1 .5-.5zM2 2a1 1 0 0 0-1 1v11a1 1 0 0 0 1 1h12a1 1 0 0 0 1-1V3a1 1 0 0 0-1-1H2z" />
                                <path d="M2.5 4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 .5.5v1a.5.5 0 0 1-.5.5H3a.5.5 0 0 1-.5-.5V4z" />
                                <path fill-rule="evenodd" d="M10.854 8.146a.5.5 0 0 1 0 .708l-3 3a.5.5 0 0 1-.708 0l-1.5-1.5a.5.5 0 0 1 .708-.708L7.5 10.793l2.646-2.647a.5.5 0 0 1 .708 0z" />
                            </svg>
                        </td>
                    </tr>
                </table>
            </div>
        </div>
        <SaveOrder :order="order" v-if="showSaveOrder" @close="closePopup()"></SaveOrder>
    </div>
</template>

<script>
import WooCommerceRestApi from "@woocommerce/woocommerce-rest-api"
import GroupAction from "./GroupAction";
import SaveOrder from "../popup/SaveOrder"

export default {
    name: 'OrderInformation',
    components: {
        WooCommerceRestApi,
        GroupAction,
        SaveOrder
    },
    props: {
        TimeId: Number,
        row: {},
    },
    data() {
        return {
            orderInfs: [],
            showSaveOrder: false,
        }
    },
    mounted: function() {
    },
    methods: {
        
        editInfo: function(name, row, value, event) {
            self = this;
            switch (name) {
                case "address":
                    self.row[row.index].editAddress = !self.row[row.index].editAddress;
                    break;
                case "name":
                    self.row[row.index].editName = !self.row[row.index].editName;
                    break;
                case "phone":
                    self.row[row.index].editPhone = !self.row[row.index].editPhone;
                    break;
                case "remarks":
                    self.row[row.index].editRemarks = !self.row[row.index].editRemarks;
                    break;
            }
        },

        saveInfo: function(name, row, event , value) {
            self = this;
            var order_id = row.orderId;
            var action = '';
            var option = '';
            switch (name) {
                case "address":
                    self.row[row.index].editAddress = !self.row[row.index].editAddress;
                    action = 'address';
                    option = value;
                    break;
                case "name":
                    self.row[row.index].editName = !self.row[row.index].editName;
                    action = 'name';
                    option = value;
                    break;
                case "phone":
                    self.row[row.index].editPhone = !self.row[row.index].editPhone;
                    action = 'phone';
                    option = value;
                    break;
                case "remarks":
                    self.row[row.index].editRemarks = !self.row[row.index].editRemarks;
                    action = 'remarks';
                    option = value;
                    break;
            }
            if(action) {
                self.showSaveOrder = true;
                 var WooCommerce = new WooCommerceRestApi({
                    url: self.$parent.$parent.config.url,
                    consumerKey: self.$parent.$parent.config.consumerKey,
                    consumerSecret: self.$parent.$parent.config.consumerSecret,
                    wpAPI: true,
                    version: 'wc/v3',
                });
                WooCommerce.put('custom/plotter/'+order_id , { action : action , value : option}).then(function(result) {
                   self.$root.$emit('saveOrderPlotter', true);
                });
            }
        },
        showOrderDetail: function(row, value, event , action) {
            let data = this.row;
            var order_id = row.orderId;

            data.forEach(function(val, key) {
                if (typeof val.orderId != 'undefined' && val.orderId === row.orderId) {
                    data[key].showDetail = value;
                }
            });
            var self = this;
            var order_id = row.orderId;
            var WooCommerce = new WooCommerceRestApi({
                url: self.$parent.$parent.config.url,
                consumerKey: self.$parent.$parent.config.consumerKey,
                consumerSecret: self.$parent.$parent.config.consumerSecret,
                wpAPI: true,
                version: 'wc/v3',
            });
            var order = {};
            if(action == 'open') {
                WooCommerce.get('custom/plotter/'+order_id).then(function(result) {
                    order = {
                        address : result.data.address,
                        name : result.data.name,
                        phone : result.data.phone,
                        remarks: result.data.remarks,
                    };
                    self.row[row.index].orderDetail = order;
                });
            } 
        },
        tickOrder: function(row) {
            var self = this;
            var order_id = row.orderId;
            var WooCommerce = new WooCommerceRestApi({
                url: self.$parent.$parent.config.url,
                consumerKey: self.$parent.$parent.config.consumerKey,
                consumerSecret: self.$parent.$parent.config.consumerSecret,
                wpAPI: true,
                version: 'wc/v3',
            });
            WooCommerce.put('custom/plotter/'+order_id , { action : 'tick' , value : self.row[row.index].orderTick ? '' : 'ok' } ).then(function(result) {
                self.row[row.index].orderTick = self.row[row.index].orderTick ? '' : 'ok';
            });
        },
        showAction: function(row, value, event) {
            let data = this.row;

            data.forEach(function(val, key) {
                data[key].showGroupAction = false;
                if (typeof val.orderId != 'undefined' && val.orderId === row.orderId) {
                    data[key].showGroupAction = value;
                }
            });
        },
        closePopup: function() {
            this.showSaveOrder = false;
        }
    }
}
</script>
<style scoped>

.order-row {
    background: white;
    border-bottom: 1px solid silver;
}

.order-detail {
    background: #f2f3f3;
    padding: 15px;
    position: relative;
}

.order-table {
    width: 100%;
}

.group {
    position: relative;
    float: right;
}
</style>



