<template>
    <div class="wrap-order">
        <div class="add-delivery">
            <span class="delivery-text" @click="showSearch">
                <b>+ Add new delivery</b>
            </span>
        </div>
        <div class="available-order" v-show="showSearchInput">
            <div class="search">
                <div class="group-button">
                    <input @change ="searchOrder($event)" class="search-input form-control" type="text" placeholder="Please select order" />
                    <input type="hidden" placeholder="Please select order" v-bind:value="valueSearch" />
                    <div class="search-action">
                        <div class="action save">
                            <svg width="1em" height="1em" viewBox="0 0 16 16" class="bi bi-clipboard" fill="currentColor" xmlns="http://www.w3.org/2000/svg" @click="saveOrderPlotter(valueSearch , row , $event)">
                                <path fill-rule="evenodd" d="M4 1.5H3a2 2 0 0 0-2 2V14a2 2 0 0 0 2 2h10a2 2 0 0 0 2-2V3.5a2 2 0 0 0-2-2h-1v1h1a1 1 0 0 1 1 1V14a1 1 0 0 1-1 1H3a1 1 0 0 1-1-1V3.5a1 1 0 0 1 1-1h1v-1z" />
                                <path fill-rule="evenodd" d="M9.5 1h-3a.5.5 0 0 0-.5.5v1a.5.5 0 0 0 .5.5h3a.5.5 0 0 0 .5-.5v-1a.5.5 0 0 0-.5-.5zm-3-1A1.5 1.5 0 0 0 5 1.5v1A1.5 1.5 0 0 0 6.5 4h3A1.5 1.5 0 0 0 11 2.5v-1A1.5 1.5 0 0 0 9.5 0h-3z" />
                            </svg>
                        </div>
                        <div class="action delete">
                            <svg width="1em" height="1em" @click="closeSearch" viewBox="0 0 16 16" class="bi bi-x-square" fill="currentColor" xmlns="http://www.w3.org/2000/svg" >
                                <path fill-rule="evenodd" d="M14 1H2a1 1 0 0 0-1 1v12a1 1 0 0 0 1 1h12a1 1 0 0 0 1-1V2a1 1 0 0 0-1-1zM2 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H2z" />
                                <path fill-rule="evenodd" d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z" />
                            </svg>
                        </div>
                    </div>
                </div>
                <ul v-show="showResult" class="search-result">
                    <li v-for="id in listOrder" v-bind:value="id.value" @click="selectOrder(id.value , $event)">{{id.label}}</li>
                </ul>
            </div>
           
            <div class="search-clear"></div>
        </div>
        <SaveOrder :row="row" v-if="showSaveOrder" @close="closePopup()"></SaveOrder>
    </div>
</template>

<script>
import WooCommerceRestApi from "@woocommerce/woocommerce-rest-api"
import SaveOrder from "../popup/SaveOrder"
export default {
    data() {
        return {
            showResult: false,
            showSearchInput: false,
            listOrder: [],
            valueSearch: '',
            active: '',
            showSaveOrder: false,
        }
    },
    props: {
        row: {},
    },
    name: 'AvailableOrder',
    methods: {
        showSearch: function() {
            this.showSearchInput = true;
        },
        closeSearch: function() {
            this.showSearchInput  =false;
            this.listOrder = [];
        },
        selectOrder: function(value , event) {
            this.valueSearch = value;
            $('ul.search-result li').each(function() {
                $(this).removeClass('active');
            })
            $(event.path[0]).addClass('active');

        },
        saveOrderPlotter: function(orderId , row , event) {
            var self = this;
            var period = row.value;
            var today = this.$parent.$parent.date;
            var WooCommerce = new WooCommerceRestApi({
                url: self.$parent.$parent.config.url,
                consumerKey: self.$parent.$parent.config.consumerKey,
                consumerSecret: self.$parent.$parent.config.consumerSecret,
                wpAPI: true,
                version: 'wc/v3',
            });
            if(orderId && period && today) {
                this.showSaveOrder = true
                WooCommerce.put('custom/plotter-search', {day : today, period : period, order_id : orderId }).then(function(result) {
                    self.$root.$emit('pushOrderPlotter', result.data , self.row.index);
                    self.$root.$emit('saveOrderPlotter', true);

                });
            } 
        },
        searchOrder: function(event) {
            let val = event.target.value;
            var self = this;
            var WooCommerce = new WooCommerceRestApi({
                url: self.$parent.$parent.config.url,
                consumerKey: self.$parent.$parent.config.consumerKey,
                consumerSecret: self.$parent.$parent.config.consumerSecret,
                wpAPI: true,
                version: 'wc/v3',
            });
            var period = {};
            var periods = [];
            self.listOrder = []
            if(val) {
                WooCommerce.get('custom/plotter-search', { key : val}).then(function(result) {
                    result.data.forEach( function( value , key ) {
                        self.listOrder.push( {
                                label : '# '+value['ID'],
                                value : value['ID']
                            }
                        );
                    })
                });
            } else {
                self.listOrder = [];
            }
            
            this.showResult = true;
        },
        closePopup: function() {
            this.showSaveOrder = false;
        }
    },
    components: {
        WooCommerceRestApi,
        SaveOrder
    }
}
</script>



