<template>
    <div class="wrapper">
        <VueDataPick v-model="date"></VueDataPick>
        <DeliveryDetail :DateTime="date"></DeliveryDetail>
        <div class="clear"></div>
    </div>
</template>

<script>
// import datepicker from 'vue-date'
import datepicker from './lib-component/DatePicker'
import VueDataPick from './lib-component/VueDatePick'
import DeliveryDetail from "./DeliveryDetail"
import WooCommerceRestApi from "@woocommerce/woocommerce-rest-api"

export default {
    name: 'DeliveryPlotter',
    data() {
        return {
            msg: 'Welcome to Your Vue.js App',
            // date: new Date().getFullYear() + '-' + (new Date().getMonth()+1) + '-' + new Date().getDate(),
            date: '',

            check: false,

            config: {
                url: 'https://botaksign.com/',
                consumerKey: 'ck_06b92636a8d2d2bf1f0328d624b18b1c3a6b9638',
                consumerSecret: 'cs_25e85f8fe5adad59c529a801242b9a427aa4d512',
                // url: 'https://dev01.netbaseteam.com/botak',
                // consumerKey: 'ck_ef00a841adb159dce5807e2e9898d04ae0a6cf6d',
                // consumerSecret: 'cs_48144b9675de9004591eaac76053953d05266f54',
                // url: 'http://botak-signgit.loc',
                // consumerKey: 'ck_77bdd053a4248dff88c9982189e5a75b9eb044bb',
                // consumerSecret: 'cs_54e8c939b4ec322237ed3a28c1385e08c8cb6c21',
            },
            ordersInDay: [],
            periods: []
        }
    },
    beforeCreate: function() {
        
    },
    mounted: function() {
        var d = new Date();
        this.date = d.getFullYear()+'-'+(d.getMonth()+1)+'-'+d.getDate();
        var self = this;
        var WooCommerce = new WooCommerceRestApi({
            url: self.config.url,
            consumerKey: self.config.consumerKey,
            consumerSecret: self.config.consumerSecret,
            wpAPI: true,
            version: 'wc/v3',
        });
        var period = {};
        var periods = [];
        WooCommerce.get('custom/period').then(function(result) {
            result.data.forEach( function( value , key) {
                period = {
                    label : value.period_label,
                    value : value.period_value,
                    showTime : false,
                    index: key,
                    data_child: {}
                };
                periods.push(period);
            })
            self.$root.$emit('updatePeriod', periods);
            self.periods = periods;
        });
    },
    watch: {
        date: function() {
            this.changeDate();
            //alert(this.date);
        }
    },
    methods: {
        getDate: function() {
            let today = new Date();
            let formatDate = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
            return formatDate;
        },

        changeDate: function() {
            this.msg = this.date;
        }
    },
    components: {
        DeliveryDetail,
        datepicker,
        VueDataPick,
        WooCommerceRestApi,
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.form-date{
    width: 60%;
    float: left;
}
.clear {
    clear: both;
}
</style>



